import React, { useEffect, useState } from 'react';
import { Input, Select, AutoSuggest } from '@components/libs/Forms';
import { useNavigate } from 'react-router-dom';
import Button from '@components/libs/Button';
import SojiBaseInit from '@modules/SojiBase';
import { Link } from 'react-router-dom';
import { useAlertState } from '@components/libs/Notify/useAlertState';
import Cookies from 'js-cookie';
import Preloader from '@components/preloader';
import useUserDetails from '@components/Hooks/useUserDetails';
import Structure from '../../../Structure';
import '../../index.css';
import styled from 'styled-components';
import { colors } from '@components/typo';

const SendButton = styled(Button)`
    width: 60%;
    height: 40px;
`;

export default function SignUp({ pageHandler, embedToGetUserToken }) {
    let createdCompanyValue;
    const useUserDetail = useUserDetails();
    const navigate = useNavigate();
    const alertState = useAlertState();
    const [state, setState] = useState({
        company: '',
        companyName: '',
        countriesList: false,
        country: false,
        email: '',
        phoneNumber: '',
        website: '',
        onRequest: false,
        companiesList: [],
        isFromExistingCompany: false,
    });

    const stateHandler = (key, value) => {
        state[key] = value;
        setState({ ...state });
    };

    const signUp = async () => {
        if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
            const SOJIBase = await SojiBaseInit;
            await SOJIBase.deleteCookie();
            useUserDetail.set(false);
        }
        const payload = {
            companyName: state.companyName,
            email: state.email,
            phoneNumber: state.phoneNumber,
            website: state.website,
            country: state.country,
        };
        stateHandler('onRequest', true);
        const SojiBase = await SojiBaseInit;
        const getter = await SojiBase.post('v1/company/authe/signup', payload);
        getter.on('static', (response) => {
            stateHandler('onRequest', false);
            const data = response.response.data;
            if (response.response.status == 201) {
                alertState.set({
                    state: true,
                    title: 'Notification',
                    desc: data,
                    type: 'success',
                    onClose: () => navigate('/company/dashboard/awaiting-activation'),
                });
            } else {
                alertState.set({
                    state: true,
                    title: 'Notification',
                    desc: data,
                    type: 'success',
                    onClose: () => window.location.reload(),
                });
            }
        });
    };

    const claimCompany = async () => {
        stateHandler('onRequest', true);

        if (typeof Cookies.get('SOJI_uft_token') != 'undefined') {
            const SOJIBase = await SojiBaseInit;
            await SOJIBase.deleteCookie();
            useUserDetail.set(false);
        }

        const payload = {
            companyName: state.companyName,
            companyEmail: state.email,
            companyPhoneNumber: state.phoneNumber,
            claimCompanyId: state.company.id,
        };

        const SojiBase = await SojiBaseInit;
        const getter = await SojiBase.post('v1/company/authe/claim_company', payload);
        getter.on('static', (response) => {
            stateHandler('onRequest', false);
            const data = response.response.data;
            if (response.response.status == 201) {
                alertState.set({
                    state: true,
                    title: 'Notification',
                    desc: data,
                    type: 'success',
                    onClose: () => navigate('/company/dashboard/awaiting-activation'),
                });
            } else {
                alertState.set({
                    state: true,
                    title: 'Notification',
                    desc: data,
                    type: 'success',
                    onClose: () => window.location.reload(),
                });
            }
        });
    };

    useEffect(() => {
        (async () => {
            const SojiBase = await SojiBaseInit;
            const getter = await SojiBase.get('v1/utils/countries');
            getter.on('static', (response) => {
                stateHandler('countriesList', response.response.data);
            });
        })();
    }, []);

    const onCompanySelected = (company) => {
        stateHandler('companyName', company);
        if (company == createdCompanyValue) {
            stateHandler('isFromExistingCompany', false);
        } else {
            stateHandler('isFromExistingCompany', true);
        }
    };

    const createdCompanyValueHandler = (value) => {
        createdCompanyValue = value;
    };

    return (
        <Structure companyAuthe>
            {state.countriesList ? (
                <div className="authe_container">
                    <div className="title">Signup as a Company</div>
                    <div className="authe_content">
                        <AutoSuggest
                            allowCreateWhileLoading={false}
                            placeholder="Company Name"
                            title="Company Name"
                            onChange={(value) => {
                                stateHandler('company', value);
                                onCompanySelected(value.companyName);
                            }}
                            value={state.company}
                            cacheOptions={false}
                            getNewOptionData={createdCompanyValueHandler}
                        />

                        {state.isFromExistingCompany ? (
                            <>
                                <Input
                                    placeholder="Company Email"
                                    title="Company Email"
                                    value={state.email}
                                    onChange={(e) => stateHandler('email', e.target.value)}
                                    desc="Seperate each email address by ,"
                                />
                                <Input
                                    placeholder="Company Phone Number"
                                    title="Company Phone Number"
                                    value={state.phoneNumber}
                                    onChange={(e) => stateHandler('phoneNumber', e.target.value)}
                                    desc="Seperate each Phone Number by ,"
                                />
                            </>
                        ) : (
                            <>
                                <Input
                                    placeholder="Company Email"
                                    title="Company Email"
                                    value={state.email}
                                    onChange={(e) => stateHandler('email', e.target.value)}
                                    desc="Seperate each email address by ,"
                                />
                                <Input
                                    placeholder="Company Phone Number"
                                    title="Company Phone Number"
                                    value={state.phoneNumber}
                                    onChange={(e) => stateHandler('phoneNumber', e.target.value)}
                                    desc="Seperate each Phone Number by ,"
                                />
                                <Select
                                    placeholder="Select Country"
                                    title="Select Country"
                                    onChange={(e) => {
                                        stateHandler('country', e.value);
                                    }}
                                    options={state.countriesList}
                                    important
                                    value={state.countriesList.filter(function (option) {
                                        return option.value === state.country;
                                    })}
                                />
                                <Input
                                    placeholder="Company Website"
                                    value={state.website}
                                    title="Company Website"
                                    onChange={(e) => stateHandler('website', e.target.value)}
                                />
                            </>
                        )}
                    </div>
                    <div className="authe_action_box">
                        <p>
                            Have an account?
                            <Link to="/company/login" className="text_default_color text_bold cursor_pointer">
                                Sign In
                            </Link>
                        </p>
                    </div>
                    {state.isFromExistingCompany ? (
                        <div className="button_container">
                            <SendButton
                                className="button"
                                title="Claim Company"
                                loading={state.onRequest}
                                onClick={() => claimCompany()}
                            />
                        </div>
                    ) : (
                        <div className="button_container">
                            <SendButton
                                className="button"
                                title="Sign Up"
                                loading={state.onRequest}
                                onClick={() => signUp()}
                            />
                        </div>
                    )}
                </div>
            ) : (
                <Preloader />
            )}
        </Structure>
    );
}
